@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-main {
  display: flex; }

.page-main__button {
  margin: 492px auto 0; }

.button {
  display: block;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  padding: 13px 0;
  outline: none; }

.page-main__button {
  width: 200px;
  background: #558CB7; }

.overlay-modal {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 3; }

.overlay-modal--close {
  display: none; }

.overlay-modal--open {
  display: block; }

.modal-step1 {
  position: absolute;
  width: 480px;
  top: 138px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  background-color: #ffffff; }

.modal-step1__wrap {
  padding: 39px 41px 0 39px;
  position: relative;
  display: flex;
  flex-wrap: wrap; }

.modal-step1__form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column; }

.modal-step1__require-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.modal-step1__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
  margin: 0; }

.modal-step1__close {
  position: absolute;
  background: url("../img/close.svg") center no-repeat;
  border: none;
  width: 19px;
  height: 19px;
  outline: none;
  cursor: pointer;
  top: 37px;
  right: 48px; }
  .modal-step1__close:hover, .modal-step1__close:focus {
    background: url("../img/close-active.svg"); }

.modal-step1__label {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px; }
